import { useState, useEffect } from "react";
import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";

import { AlertCircle, Loader2, Info, ArrowLeft, Users, ListFilter, Eye } from "lucide-react";
import { toast } from "sonner";

import {
  createCampaign,
  type CreateCampaignRequest,
  type Customer
} from "@/api/services/outbound/campaignService";
import { fetchAllAgentsFromVapi } from "@/api/services/agents/agentService";
import { getAllPhoneNumbers } from "@/api/services/phoneNumbers/phoneNumberService";
import { getAllContactLists, getContactsForList } from "@/api/services/contact/contactService";
import { useAuth } from "@/context/AuthContext";

export function CreateCampaignPage() {
  const navigate = useNavigate();
  const [campaignName, setCampaignName] = useState("");
  const [campaignType, setCampaignType] = useState("manual");
  const [phoneNumberId, setPhoneNumberId] = useState("");
  const [assistantId, setAssistantId] = useState("");
  const [customers, setCustomers] = useState<Customer[]>([]);
  const [selectedListId, setSelectedListId] = useState("");
  const [sendOption, setSendOption] = useState("now");
  const [autoLaunch, setAutoLaunch] = useState(false);

  const queryClient = useQueryClient();
  const { user } = useAuth();

  // Fetch assistants
  const { data: assistantsData, isLoading: assistantsLoading } = useQuery({
    queryKey: ["assistants", "vapi", user?.id],
    queryFn: () => fetchAllAgentsFromVapi(),
    retry: (failureCount, error: any) => {
      // Don't retry for expected scenarios (normal for new users)
      const expectedStatuses = [404, 204, 402]; // 402 = Payment Required (no credits)
      if (expectedStatuses.includes(error?.response?.status)) {
        return false;
      }
      return failureCount < 1; // Retry once for other errors
    },
    enabled: !!user?.id,
  });

  // Fetch phone numbers
  const { data: phoneNumbersData, isLoading: phoneNumbersLoading } = useQuery({
    queryKey: ["phoneNumbers"],
    queryFn: () => getAllPhoneNumbers(),
    retry: 1,
  });

  // Fetch contact lists
  const { data: listsData, isLoading: listsLoading } = useQuery({
    queryKey: ["contactLists"],
    queryFn: () => getAllContactLists(),
    retry: 1,
  });

  // Fetch contacts for selected list
  const { data: contactsData, isLoading: contactsLoading } = useQuery({
    queryKey: ["listContacts", selectedListId],
    queryFn: () => getContactsForList(selectedListId),
    enabled: !!selectedListId,
    retry: 1,
  });

  // Effect to load contacts when list is selected
  useEffect(() => {
    console.log('Contacts data received:', contactsData); // Debug log

    if (contactsData?.success && contactsData?.data?.contacts) {
      const contactsArray = contactsData.data.contacts;
      console.log('Processing contacts array:', contactsArray); // Debug log

      const contacts = contactsArray.map((contact: any) => ({
        name: `${contact.firstName || contact.first_name || ''} ${contact.lastName || contact.last_name || ''}`.trim() ||
              contact.fullName || contact.email || 'Unknown',
        number: contact.contact_number || contact.phoneNumber || contact.phone,
        email: contact.email || ''
      }));
      console.log('Mapped contacts:', contacts); // Debug log
      setCustomers(contacts);
    } else if (contactsData && !contactsData.success) {
      // Handle API error response
      console.log('API error response:', contactsData);
      setCustomers([]);
    } else if (selectedListId && contactsData && contactsData.success && (!contactsData.data?.contacts || contactsData.data.contacts.length === 0)) {
      // Handle empty list
      console.log('Empty list detected');
      setCustomers([]);
    }
  }, [contactsData, selectedListId]);

  // Create campaign mutation
  const createMutation = useMutation({
    mutationFn: createCampaign,
    onSuccess: (data) => {
      queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });

      // The backend handles auto-launch, so we just show the appropriate message
      const message = data?.message || "Campaign created successfully";
      toast.success(message);

      navigate("/outbound");
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to create campaign");
    },
  });

  const handleListSelect = (listId: string) => {
    setSelectedListId(listId);
    setCustomers([]); // Clear previous customers while loading
  };

  const handleSubmit = () => {
    if (!campaignName.trim()) {
      toast.error("Campaign name is required");
      return;
    }

    if (!phoneNumberId) {
      toast.error("Phone number is required");
      return;
    }

    if (!assistantId) {
      toast.error("Assistant is required");
      return;
    }

    if (!selectedListId) {
      toast.error("Please select a contact list");
      return;
    }

    if (customers.length === 0) {
      toast.error("Selected list has no contacts");
      return;
    }

    // Validate phone numbers are in E.164 format
    const invalidNumbers = customers.filter(customer => {
      const number = customer.number;
      return !number || !number.startsWith('+') || !/^\+[1-9]\d{1,14}$/.test(number);
    });

    if (invalidNumbers.length > 0) {
      toast.error(`Invalid phone numbers found. Please ensure all numbers are in E.164 format (e.g., +1234567890). Found ${invalidNumbers.length} invalid numbers.`);
      return;
    }

    const campaignData: CreateCampaignRequest = {
      name: campaignName,
      phoneNumberId,
      assistantId,
      customers,
      autoLaunch,
    };

    console.log('Creating campaign with data:', campaignData);
    createMutation.mutate(campaignData);
  };

  const assistants = assistantsData?.data || [];
  const phoneNumbers = phoneNumbersData?.data || [];
  const lists = listsData?.data || [];

  return (
    <div className="app-shell-bg min-h-screen w-full overflow-auto">
      <div className="p-6 max-w-[1600px] mx-auto space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => navigate("/outbound")}
              className="flex items-center gap-2 text-white/65 hover:text-white neo-topbar-btn"
            >
              <ArrowLeft className="h-4 w-4" />
              Back to Outbound
            </Button>
            <div>
              <h1 className="text-3xl font-bold tracking-tight text-white">Create Campaign</h1>
              <p className="text-white/65">
                Create a new outbound campaign • Contacts loaded: {customers.length}
                {selectedListId && lists.length > 0 && (
                  <span className="ml-2">
                    from "{lists.find((l: any) => l.id === selectedListId)?.listName || lists.find((l: any) => l.id === selectedListId)?.list_name}"
                  </span>
                )}
              </p>
            </div>
          </div>
        </div>

        {/* Main Content Area */}
        <div className="flex gap-6">
          {/* Left Side - Campaign Form */}
          <div className="w-1/2">
            <div className="neo-card p-6">
              <div className="space-y-6">
                  {/* Campaign Name */}
                  <div className="space-y-2">
                    <Label htmlFor="campaign-name" className="text-sm font-medium text-white/65">
                      Campaign Name
                    </Label>
                    <Input
                      id="campaign-name"
                      placeholder="Campaign Name"
                      value={campaignName}
                      onChange={(e) => setCampaignName(e.target.value)}
                      className="w-full bg-white/10 border-white/20 text-white placeholder:text-white/40"
                    />
                  </div>

                  {/* Campaign Type */}
                  <div className="flex flex flex-col space-y-3">
                    <Label className="text-sm font-medium text-white/65">Campaign Type</Label>
                    <RadioGroup value={campaignType} onValueChange={setCampaignType} className="flex items-start space-x-8 border-t border-white/10 pt-4">
                      <div className=" flex items-start space-x-2">
                        <RadioGroupItem value="manual" id="manual" className="mt-1 text-cyan-400" />
                        <div className="space-y-1">
                          <Label htmlFor="manual" className="font-medium cursor-pointer text-white">Manual</Label>
                          <p className="text-sm text-white/65">
                            Start campaign now or schedule for later
                          </p>
                        </div>
                      </div>
                      {/* <div className="flex items-start space-x-2">
                        <RadioGroupItem value="live" id="live" className="mt-1 text-blue-600" />
                        <div className="space-y-1">
                          <Label htmlFor="live" className="font-medium cursor-pointer text-gray-900">Live</Label>
                          <p className="text-sm text-gray-500">
                            Triggered via webhooks from your website or forms
                          </p>f
                        </div>
                      </div> */}
                    </RadioGroup>
                  </div>

                  {/* Phone Number */}
                  <div className="space-y-2">
                    <div className="flex items-center gap-2">
                      <Label htmlFor="phone-number" className="text-sm font-medium text-white/65">
                        Phone Number
                      </Label>
                      <div className="flex items-center gap-1">
                        <Info className="h-4 w-4 text-white/40" />
                        <span className="text-xs text-cyan-400 bg-cyan-500/10 px-2 py-1 rounded">
                          From AI CRUITMENT
                        </span>
                      </div>
                    </div>
                    <Select value={phoneNumberId} onValueChange={setPhoneNumberId}>
                      <SelectTrigger className="border-gray-300 text-white focus:border-blue-500 focus:ring-blue-500">
                        <SelectValue placeholder={phoneNumbersLoading ? "Loading..." : "Select phone number"} />
                      </SelectTrigger>
                      <SelectContent>
                        {phoneNumbersLoading ? (
                          <SelectItem value="loading" disabled>
                            <Loader2 className="h-4 w-4 animate-spin mr-2" />
                            Loading...
                          </SelectItem>
                        ) : phoneNumbers.length === 0 ? (
                          <SelectItem value="no-numbers" disabled>
                            No phone numbers available
                          </SelectItem>
                        ) : (
                          phoneNumbers.map((phoneNumber: any) => (
                            <SelectItem key={phoneNumber.id} value={phoneNumber.id}>
                              <div className="flex flex-col">
                                <span className="font-medium">
                                  {phoneNumber.number || phoneNumber.twilioPhoneNumber}
                                </span>
                                {phoneNumber.name && (
                                  <span className="text-xs text-gray-500">
                                    {phoneNumber.name}
                                  </span>
                                )}
                              </div>
                            </SelectItem>
                          ))
                        )}
                      </SelectContent>
                    </Select>

                  </div>

                  {/* Select Contact List */}
                  <div className="space-y-2">
                    <div className="flex items-center gap-2">
                      <Label htmlFor="contact-list" className="text-sm font-medium text-white/65">
                        Contact List
                      </Label>
                      <div className="flex items-center gap-1">
                        <Info className="h-4 w-4 text-white/40" />
                        <span className="text-xs text-cyan-400 bg-cyan-500/10 px-2 py-1 rounded">
                          From Lists
                        </span>
                      </div>
                    </div>
                    <Select value={selectedListId} onValueChange={handleListSelect}>
                      <SelectTrigger className="border-gray-300 text-white  ">
                        <SelectValue placeholder={listsLoading ? "Loading..." : "Select contact list"} />
                      </SelectTrigger>
                      <SelectContent>
                        {listsLoading ? (
                          <SelectItem value="loading" disabled>
                            <Loader2 className="h-4 w-4 animate-spin mr-2" />
                            Loading...
                          </SelectItem>
                        ) : lists.length === 0 ? (
                          <SelectItem value="no-lists" disabled>
                            No contact lists available
                          </SelectItem>
                        ) : (
                          lists.map((list: any) => (
                            <SelectItem key={list.id} value={list.id}>
                              <div className="flex items-center space-x-3">
                                <div className="h-6 w-6 bg-gradient-to-br from-primary/20 to-primary/10 rounded flex items-center justify-center">
                                  <ListFilter className="h-3 w-3 text-primary" />
                                </div>
                                <div className="flex flex-col">
                                  <span className="font-medium">{list.listName || list.list_name}</span>
                                  <span className="text-xs text-white/65">
                                    <Users className="h-3 w-3 inline stroke-8  mr-1" />
                                    {list.contactCount || list.contacts_count || 0} contacts
                                  </span>
                                </div>
                              </div>
                            </SelectItem>
                          ))
                        )}
                      </SelectContent>
                    </Select>

                    {selectedListId && contactsLoading && (
                      <div className="flex items-center gap-2 p-3 bg-cyan-500/10 border border-cyan-500/20 rounded-md">
                        <Loader2 className="h-4 w-4 animate-spin text-cyan-400" />
                        <span className="text-sm text-white">Loading contacts...</span>
                      </div>
                    )}


                  </div>

                  {/* Assistant */}
                  <div className="space-y-2">
                    <div className="flex items-center gap-2">
                      <Label htmlFor="assistant" className="text-sm font-medium text-white/65">
                        Assistant
                      </Label>
                      <span className="text-xs text-cyan-400 bg-cyan-500/10 px-2 py-1 rounded">
                        From AI CRUITMENT
                      </span>
                    </div>
                    <Select value={assistantId} onValueChange={setAssistantId}>
                      <SelectTrigger className="border-gray-300 text-white  focus:border-blue-500 focus:ring-blue-500">
                        <SelectValue placeholder={assistantsLoading ? "Loading..." : "Select assistant"} />
                      </SelectTrigger>
                      <SelectContent>
                        {assistantsLoading ? (
                          <SelectItem value="loading" disabled>
                            <Loader2 className="h-4 w-4 animate-spin mr-2" />
                            Loading...
                          </SelectItem>
                        ) : assistants.length === 0 ? (
                          <SelectItem value="no-assistants" disabled>
                            No assistants available
                          </SelectItem>
                        ) : (
                          assistants.map((assistant: any) => (
                            <SelectItem key={assistant.id} value={assistant.id}>
                              <div className="flex flex-col">
                                <span className="font-medium">{assistant.name}</span>
                                {assistant.model?.model && (
                                  <span className="text-xs text-gray-500">
                                    {assistant.model.model} • {assistant.voice?.voiceId || 'Default Voice'}
                                  </span>
                                )}
                              </div>
                            </SelectItem>
                          ))
                        )}
                      </SelectContent>
                    </Select>
                  </div>

                  {/* Choose when to send */}
                  <div className="space-y-3">
                    <Label className="text-sm font-medium text-white/65">Choose when to send</Label>
                    <RadioGroup value={sendOption} onValueChange={setSendOption} className="flex items-center space-x-6">
                      <div className="flex items-center space-x-2">
                        <RadioGroupItem value="now" id="send-now" className="text-cyan-400" />
                        <Label htmlFor="send-now" className="cursor-pointer text-white">Send Now</Label>
                      </div>
                      {/* <div className="flex items-center space-x-2">
                        <RadioGroupItem value="later" id="send-later" className="text-blue-600" />
                        <Label htmlFor="send-later" className="cursor-pointer text-gray-900">Schedule for later</Label>
                      </div> */}
                    </RadioGroup>
                  </div>

                  {/* Auto-launch option */}
                  <div className="flex items-center space-x-2 pt-4 border-t border-white/10">
                    <input
                      type="checkbox"
                      id="auto-launch"
                      checked={autoLaunch}
                      onChange={(e) => setAutoLaunch(e.target.checked)}
                      className="h-4 w-4 text-cyan-400 focus:ring-cyan-500 border-white/20 rounded bg-white/10"
                    />
                    <Label htmlFor="auto-launch" className="text-sm text-white/65 cursor-pointer">
                      Automatically launch campaign after creation
                    </Label>
                  </div>

                  {/* Launch Button */}
                  <Button
                    onClick={handleSubmit}
                    disabled={createMutation.isPending}
                    className="w-full neo-btn py-3 text-base font-medium rounded-lg"
                  >
                    {createMutation.isPending && <Loader2 className="h-4 w-4 mr-2 animate-spin" />}
                    Launch campaign
                  </Button>

              </div>
            </div>
          </div>

          {/* Right Side - Contact Preview Panel */}
          <div className="w-1/2">
            <div className="neo-card">
              <div className="px-6 py-4 border-b border-white/10">
                <h3 className="text-lg font-semibold text-white">Contact Preview</h3>
                <p className="text-sm text-white/65">
                  {!selectedListId ? (
                    "Select a list to preview contacts"
                  ) : contactsLoading ? (
                    <span className="flex items-center gap-2">
                      <Loader2 className="h-4 w-4 animate-spin" />
                      Loading contacts...
                    </span>
                  ) : (
                    <>
                      {customers.length} contacts from selected list
                      {selectedListId && lists.length > 0 && (
                        <span className="ml-1 font-medium">
                          "{lists.find((l: any) => l.id === selectedListId)?.listName || lists.find((l: any) => l.id === selectedListId)?.list_name}"
                        </span>
                      )}
                    </>
                  )}
                </p>
              </div>

              <div className="p-6">
                {!selectedListId ? (
                  <div className="h-96 flex items-center justify-center border border-white/10 rounded-lg bg-white/5">
                    <div className="text-center">
                      <Eye className="h-8 w-8 text-white/40 mx-auto mb-3" />
                      <p className="text-lg font-medium text-white mb-2">Select List for Preview Contacts</p>
                      <p className="text-sm text-white/65">Choose a contact list from the form to preview contacts here</p>
                    </div>
                  </div>
                ) : contactsLoading ? (
                  <div className="h-96 flex items-center justify-center border border-white/10 rounded-lg bg-white/5">
                    <div className="text-center">
                      <Loader2 className="h-8 w-8 animate-spin text-white/40 mx-auto mb-3" />
                      <p className="text-sm text-white/65">Loading contacts...</p>
                    </div>
                  </div>
                ) : customers.length === 0 ? (
                  <div className="h-96 flex items-center justify-center border border-white/10 rounded-lg bg-white/5">
                    <div className="text-center">
                      <Users className="h-8 w-8 text-white/40 mx-auto mb-3" />
                      <p className="text-sm text-white/65">No contacts found in this list</p>
                    </div>
                  </div>
                ) : (
                  <div className="h-96 overflow-auto border border-white/10 rounded-lg neon-fire-scroll">
                    <table className="w-full text-sm">
                      <thead className="bg-white/5 border-b border-white/10">
                        <tr>
                          <th className="px-4 py-3 text-left font-medium text-white/65">Name</th>
                          <th className="px-4 py-3 text-left font-medium text-white/65">Phone Number</th>
                          <th className="px-4 py-3 text-left font-medium text-white/65">Email</th>
                        </tr>
                      </thead>
                      <tbody>
                        {customers.map((customer, index) => (
                          <tr key={index} className="border-b border-white/5 hover:bg-white/5">
                            <td className="px-4 py-3 text-white">{customer.name}</td>
                            <td className="px-4 py-3 text-white/65">{customer.number}</td>
                            <td className="px-4 py-3 text-white/65">{customer.email}</td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

